import * as React from 'react';

import Button from '@mui/material/Button';
import { TextField } from '@mui/material';
import './contato.css';
import { TextareaAutosize } from '@mui/material';





function Formularios() {
  return (
  <>
  <section className='contato'><div className='sectionfomr'>
    
  <div className='formulario vignette'>
  <TextField className="standard-basic" label="Nome" type="text" id="Nome" variant="standard" required/>
  <TextField className="standard-basic" label="Celular" type="tel" id="telefone" variant="standard" required/>
  <TextField className="standard-basic" label="Email" type="email" id="email" variant="standard" required/>
  <TextField className="standard-basic" label="Assunto" type="text" id="assunto" variant="standard" required/>
  <TextField className="standard-basic" label="Mensagem" type="text" id="menssagem" variant="standard" placeholder="Digite aqui sua mensagem..." disabled/>
  <TextareaAutosize
  
  label="Mensagem"
  variant="standard"
  minRows={3}
  placeholder="digite aqui sua mensagem..."
  
/>


  <Button variant="contained"> Enviar Mensagem</Button>
</div>
  
  </div>
  </section>
  </>)

  }

export default Formularios;